.filter-button {
  display: inline-block;
  background-color: var(--accent-color);
  padding: 3px 5px;
  margin: 5px;
  max-width: 50px;
  text-align: center;
  font-weight: 600;
  border-radius: 3px;
  font-size: 1.5rem;
  text-transform: capitalize;
  cursor: pointer;
}

.filter-button.active {
  background-color: var(--highlight-color);
}